
.footer {
  width: auto;
  height: 80px;
  background-color: $sub-color;
  color: $other-color;
  padding: 25px;
  span {
    font-size: 18px;
  }
  a {
    float: right;
    text-decoration: none;
    font-size: 18px;
    margin-right: 60px;
    color: $other-color;
    &:hover {
      text-decoration: none;
      color: $main-color;
    }
  }
}