
/* Start About Section  */

section.about {
  padding: 60px 0px;
  .container {
    h2 {
      text-align: center;
      color: $main-color;
      font-weight: bold;
      font-size: 32px;
    }
    .row {
      img {
        width: 100%;
        height: 100%;
      }
      p {
        line-height: 2;
        font-size: 24px;
        font-weight: bold;
        margin-top: 20px;
        color: #777;
        bdi {
          color: $main-color;
        }
      }
    }
  }
}

/* End About Section  */


/* Start my-story Section */

section.my-story {
  padding: 40px 0px;
  .container {
    h2.head-border {
      text-align: center;
      color: $main-color;
      font-weight: bold;
      font-size: 32px;
    }
    .row {
      h2 {
        text-align: right;
        font-weight: bold;
      }
      p {
        line-height: 2;
        font-size: 18px;
      }
      .imges {
        text-align: center;
        margin-top: 40px;
        margin-bottom: 30px;
        img {
          width: 320px;
          height: 230px;
        }
        .besido {
          max-width: 320px;
          margin: 0 auto;
          padding: 8px 0;
          background-color: $second-color;
          .text {
            display: inline;
            padding: 12px 64px;
            font-weight: bold;
            font-size: 30px;
            background-color: $second-color;
            color: $other-color;
          }
        }
      }
    }
  }
}


/* Start my-story Section */

/* Start my Product Section */

section.my-product {
  padding: 40px 0;
  .container {
    h2.head-border {
      text-align: center;
      color: $main-color;
      font-weight: bold;
      font-size: 32px;
    }
    .row {
      .box {
        height: 420px;
        margin-bottom: 30px;
        border-radius: 3px;
        border: 1px solid $gray-color;
        &:hover {
          border: 1px solid $main-color;
        }
        .head {
          height: 180px;
          padding-top: 20px;
          text-align: center;
          img {
            max-width: 100%;
            max-height: 160px;
          }
        }
        .range {
          height: 200px;
          padding-top: 30px;
          cursor: pointer;
          @extend %transition;
          h3  {
            text-align: center;
            font-weight: bold;
            color: $main-color;
          }
          button {
            padding: 12px 35px;
            background-color: transparent;
            border: 1px solid $main-color;
            border-radius: 3px;
            margin: 35px auto 0;
            display: block;
            font-weight: bold;
            font-size: 18px;
            outline: none;
            color: $main-color;
            @extend %transition;
            &:hover {
              color: $other-color;
              background-color: $main-color;
            }
          }
        }
      }
    }
  }
}

/* End my Product Section */

/* Start Contact Us */

section.contact-us {
  padding: 40px 0;
  .container {
    h2 {
      font-weight: bold;
      font-size: 42px;
      color: $main-color;
    }
    p {
      line-height: 2;
      font-size: 28px;
      text-align: center;
      font-weight: bold;
      margin-bottom: 40px;
    }
    .row {
      form {
        input {
          width: 100%;
          height: 50px;
          display: block;
          padding: 10px 15px;
          margin-bottom: 25px;
          font-size: 18px;
          border: none;
          border-bottom: 1px solid $main-color;
          outline: none;
          &:focus {
            outline: $main-color;
          }
        }
        textarea {
          width: 100%;
          height: 200px;
          font-size: 18px;
          padding: 10px 15px;
          margin-bottom: 20px;
          border: none;
          border-bottom: 1px solid $main-color;
          &:focus {
            outline: $main-color;
          }
        }
      }
    }
    button {
      padding: 10px 25px;
      border: 1px solid $main-color;
      background: linear-gradient(left, $main-color, $red-color);
      color: $other-color;
      border-radius: 3px;
      margin: 20px auto;
      display: block;
      font-weight: bold;
      font-size: 20px;
      &:hover {
        background: linear-gradient(right, $main-color, $red-color);
      }
    }
  }
}


/* End Contact Us */